<template>
  <div>
    <mu-appbar style="width: 100%;" :color="$store.state.color" class="header">
      <a @click="()=>{$router.go(-1)}">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span>调度详情</span>
    </mu-appbar>
    <div class="Content">
      <div class="containerMain">
        <div class="flexContainer">
          <div class="titleselect">
            <span>调度时间：</span>
          </div>
          <div class="select">
            <input type="text" class="input" disabled v-model="Time" placeholder="选择调度时间" />
          </div>
        </div>
        <div class="flexContainer">
          <div class="titleselect">
            <span>调度地点：</span>
          </div>
          <div class="select" id="selectAdress">
            <input type="text" class="input" disabled v-model="adrees" placeholder="选择调度地点" />
          </div>
        </div>
      </div>
      <div class="containerMain">
        <div class="flexContainer">
          <div class="titleselect">
            <span>调度人：</span>
          </div>
          <div class="select">
            <input type="text" class="input" disabled v-model=" publisher" placeholder="选择调度时间" />
          </div>
        </div>
        <div class="flexContainer">
          <div class="titleselect">
            <span>调度电话：</span>
          </div>
          <div class="select" id="selectAdress">
            <input type="text" class="input" disabled v-model="publisherPhone" placeholder="选择调度地点" />
          </div>
        </div>
      </div>
      <div class="containerMain">
        <div class="flexContainer">
          <div class="titleselect">
            <span>调度车辆：</span>
          </div>
          <div class="select" id="selectCarid">
            <input type="text" class="input" disabled v-model="Car" placeholder="选择调度车辆" />
          </div>
        </div>
        <div class="flexContainer">
          <div class="titleselect">
            <span>调度车牌号：</span>
          </div>
          <div class="select" id>
            <input type="text" class="input" disabled v-model="vehicle" placeholder="选择调度车牌号" />
          </div>
        </div>
      </div>
      <div class="containerMain">
        <div class="flexContainer">
          <div class="titleselect">
            <span>驾驶人：</span>
          </div>
          <div class="select" id="selectCarid">
            <input type="text" class="input" disabled v-model="vehiclePerson" placeholder />
          </div>
        </div>
        <div class="flexContainer">
          <div class="titleselect">
            <span>驾驶人电话：</span>
          </div>
          <div class="select" id>
            <input type="text" class="input" disabled v-model="vehiclePhone" placeholder="选择调度车牌号" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import Vue from "vue";
import { Toast } from "vant";

Vue.use(Toast);
export default {
  data() {
    return {
      Time: "",
      adrees: "",
      AdressArr: [],
      Car: "",
      vehicle: "",
      token: "",
      minDate: new Date("1970-07-01"),
      maxDate: new Date(2019, 10, 1),
      currentDate: new Date(),
      open: false,
      adreesTo: "",
      vehiclePerson: "",
      publisher:"",
      publisherPhone:"",
      gmtCreate:"",
      vehiclePerson:"",
      vehiclePhone:""
    };
  },
  methods: {
    forData(id, res) {
      for (let index = 0; index < res.length; index++) {
        var arrId = res[index].id;
        if (id === arrId) {
          this.AdressArr = res[index];
        }
      }
      this.Time = this.AdressArr.gmtDispatch;
      this.adrees = this.AdressArr.address;
      this.Car = this.AdressArr.vehicleId;

      this.vehicle = this.AdressArr.vehicleNo;
      this.vehiclePerson = this.AdressArr.vehiclePerson;
      this.vehiclePhone = this.AdressArr.vehiclePhone;
    //   vehiclePhone

      this.publisherPhone = this.AdressArr.publisherPhone;
      this.publisher = this.AdressArr.publisher;

      this.gmtCreate = this.AdressArr.gmtCreate;
    }
  },
  mounted() {
    var id = this.$route.query.id;
    
    this.token = sessionStorage.getItem("token");
    console.log("111",this.$store.state.dispatching);
    // this.AdressArr = this.$store.state.dispatching;
    this.forData(id, this.$store.state.dispatching);
  }
};
</script>
<style scoped>
@import "../../../static/css/model.css";
@import "../../../static/css/maintain.css";
.submit {
  background: rgb(78, 217, 149);
  color: white;
  border-radius: 5px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  margin-top: 20px;
}
.Content {
  padding: 10px;
}
/* @import "../../../static/css/selectCommon.css"; */
</style>


